@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,500;0,700;1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mina:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mina:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
}

.cardContainer{
    font-family: 'Mina', sans-serif;
    margin:0 15px;
}
@media screen and (max-width:850px) {
    #homeCard{
        grid-template: 
        "f f"
        "f f"
        "f f"
        ;
    }
    #cardContainer{
        padding: 0;
        height: 160px;
        width: 150px;
    }
    #cardImg img{
        width: 150px;
        height: auto;
    }
}
.homeCard{
    text-align: center;
    display: grid;
    grid-template:  "f f f"
                    "f f f";
}
.cardContainer{
    background-color: rgb(245, 245, 245);
    margin-bottom: 25px;
    padding: 10px 0;
    box-shadow: 5px 5px 15px 5px #00000033;
    transition: all .3s ease-in-out;
}
.cardContainer:hover{
    box-shadow: 5px 5px 15px 5px #ff00007e;
}
.cardImg img{
    height: auto;
    width: 150px;
}
.cardTitle{
    font-size: 14px;
    font-weight: 700;
}
.cardPriceRange{
    font-size: 14px;
    font-weight: 500;
}
.cardlink{
    text-decoration: none;
}

.LogoText{
    text-align: center;
    font-family: 'Roboto Mono', monospace;
    font-weight: 700;
    font-size: 48px;
    letter-spacing: 1px;
    color: rgb(36, 36, 36);
}
.LogoTextTernary{
    font-family: 'Roboto Mono', monospace;
    font-weight: 300;
    font-size: 28px;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 25px;
}

.navContainer{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding:1vh 10vw;
    font-family: 'Roboto Mono', monospace;
    font-size: 18px;
    font-weight: 500;
    background: rgba(215, 215, 215, 0.85);
}
#notHidden{
    display: none;
}
@media only screen and (max-width: 850px) {
    #hidden{
        display: none;
    }
    #notHidden{
        display: block;
    }
}

.navLinkList{
    display: flex;
    flex-direction: row;
    list-style-type:none ;
    background: transparent;
}

.navLinkListIteam{
    margin: 0 10px;
    text-transform: uppercase;
    letter-spacing: 1px;
    transition: all .3s ease-in-out;
    background: transparent;
}


.navLinkListIteam:hover a{
    color: green;
}

.navLinkListIteam a{
    text-decoration: none;
    color: black;
}
.navBtn{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.btn{
    padding: 5px 15px;
    border: 1px solid black;
    border-radius: 3px;
    background: rgba(215, 215, 215, 0.85);
    color: black;
    font-family: 'Roboto Mono', monospace;
    font-weight: 300;
    letter-spacing: 1px;
    text-transform: uppercase;
    margin-right: 10px;
}
.btn:hover{
    border: 1px solid black;
    color: whitesmoke;
    background: black;
}
.hamburger{
    border: none;
}

.hamburger:hover{
    background: rgba(0, 0, 0, 0.301);
}

.menuContainer{
    position: fixed;
    left: 0;
    top: 0;
    background: rgb(70, 70, 70);
}
.menu{
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    background: rgb(210, 210, 210);
}
.menu button{
    background: transparent;
    border: none;
    padding: 25px;
    text-align: center;
    align-items: center;
    justify-content: center;
}

.menu ul{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
    list-style-type: none;
}
.menu ul li{
    padding: 15px;
}
.navLinkA{
    text-decoration: none;
    color: black;
    font-family: 'Roboto Mono', monospace;
    font-weight: 300;
    letter-spacing: 2px;
    font-size: 26px;
}
.menu ul li .navLinkA{
    text-decoration: none;
    color: black;
    font-family: 'Roboto Mono', monospace;
    font-weight: 300;
    letter-spacing: 2px;
    font-size: 26px;
}
.line::after{
    content: '';
    height: 1px;
    width: 100vw;
    color: red;
}
.hero{
    padding:0 75px 0 75px;
    text-align: center;
}

.hero img{
    width: 80%;
    border-radius: 0 0 10px 10px;
}

.heroDes img{
    width: 10%;
    border-radius: 50%;
}

.heroDes{
    padding: 0 275px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}
.topup-section{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    font-family: 'Mina', sans-serif;
}
.topupformTitle{
    font-size: 28px;
    text-align: center;
    color: rgb(26, 26, 26);
}
.mainForm{
    margin: 10px 25px;
}
.name .nameTitle{
    font-size: 18px;
    margin: 25px 0 10px 0;
    text-transform: uppercase;
}

.name input{
    height: 25px;
    width: 295px;
    padding: 5px 15px;
    border: none;
    background-color: rgba(140, 200, 255, 0.281);
    border-radius: 0 0 0 5px;
    border-left: 1px solid black ;
    border-bottom: 1px solid black ;
}
.name input:focus{
    outline: none;
    border-bottom:1px solid green;
    border-left: 1px solid green;
}
.paymentMethod{
    margin-top: 10px;
    display: flex;
    flex-direction: row;
    align-items: center;
}
.paymentMethod .paymentImg{
    margin: 5px;
}
.paymentImg img{
    width: 100px;
    height: auto;
}
.topupFormSubmitBtn{
    margin-top: 25px;
    padding: 7px 20px;
    background-color: royalblue;
    color: white;
    text-transform: uppercase;
    border: 1px solid transparent;
    transition: all .3s ease-in-out;
    border-radius: 3px;
}

.topupFormSubmitBtn:hover{
    color: royalblue;
    background-color: white;
    border: 1px solid royalblue;
}
.paymentTitle{
    font-size: 28px;
    font-weight: 500;
}
.totalCount p{
    margin-top: 25px;
    font-size: 18px;
}
.paymentSelectContainer{
    width: 250px;
    height: auto;
}
.paymentSelect{
    font-size: 18px;
}
.diamondCount{
    display: grid;
    grid-template: "f f f"
                    "f f f"
                    "f f f";
}
.diamnodCard{
    height: 50px;
    width: 90px;
    text-align: center;
    margin: 10px;
    box-shadow: 3px 3px 5px 5px rgba(0, 0, 0, 0.5);
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.diamnodCard:hover{
    box-shadow: 3px 3px 5px 5px rgba(60, 221, 108, 0.5);

}   
.diamnodCard img{
    width: 15px;
    height: auto;
    margin: 0;
    margin-left: 5px;

}
.diamnodCard .diamondValue{
    font-size: 14px;
    margin: 0;
}
.diamondCountTitle{
    font-size: 12px;
}
.mb0{
    margin-bottom: 0;
}
.signInForm{
    text-align: center;  
}
.signInForm form{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}
.signInForm form p{
    margin-top: 10px;
    margin-bottom: 5px;
}

.signInForm form button{
    margin-top: 25px;

}

.signInForm input{
    padding: 5px 10px;
}
.admin-section{
    text-align: center;
}
.admin-section h1{
    font-size: 24px;
    font-weight: 700;

}

.admin-section table{
    width: 90%;
    margin:10px 45px;
}
.admin-section table, th, td{
    border: 1px solid red;
    padding: 10px;
}
.cancel{
    background-color: red;
    color: white;
}
.pending{
    background-color: rgba(255, 255, 0, 0.493);
    color: black;
}
.done{
    background-color: rgb(1, 255, 1);
}

td img{
    width: 25px;
    height: auto;
    margin: 10px;
}
.adminBtn{
    margin: 25px;
}

.btn a{
    text-decoration: none;
    color: black;
}
.btn:hover a{
    color: white;
}
